@import './style/Variables.scss';
@import './style/Mixins.scss';

html {
  overscroll-behavior: contain;
}

body {
  background-color: $light;
  position: fixed;
  height: 100%;
}

#root {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.container {
  height: 600px;
  background-color: aliceblue;
}

* {
  font-family: 'Roboto', sans-serif;
}